
.mobileicon {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.mobileicon > img {
    height: 80px;
    width: 80px;
    object-fit: contain;
}

.mobileicon > p {
    font-size: 12px;
    background: white;
    padding: 0 5px;
    border-radius: 2px;
}

.mobileicon__link {
    color: black;
}
